// 加载动画
export default {
	data() {
		return {
			imgList:[{id:0,atts:''},{id:1,atts:'https://tool.changbaishan.gov.cn/tongcheng//upload/0807e38e-3bf2-46bf-93a0-9271e89cf812.jpg'}]
		}
	},
	created() {
		this.imgList=this.imgList.map(item=>{
			return {...item,loadingShow:true,loadingTime:0}
		})
	},
	methods: {
		loaded(e) {
			this.imgList.forEach(item=>{
				if(item.id==e.id){
					item.loadingShow=false
				}
			})
		},
		errImg(){
			
		}
	}
}
